"use client";

import { signIn, signOut, useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import Link from "next/link";

export default function Navbar() {
  const { data: session } = useSession();
  const router = useRouter();

  return (
    <nav className="bg-white shadow-md py-4 px-6 flex justify-between items-center">
      {/* Logo */}
      <Link href="/" className="text-2xl font-bold text-blue-600">
        MyApp
      </Link>

      {/* Menu */}
      <div className="flex items-center gap-6">
        <Link href="/" className="text-gray-700 hover:text-blue-500 transition">
          Home
        </Link>
        {session && (
          <>
            <Link href="/dashboard" className="text-gray-700 hover:text-blue-500 transition">
              Dashboard
            </Link>
            {session.user?.role === "admin" && (
              <Link href="/admin" className="text-gray-700 hover:text-blue-500 transition">
                Admin
              </Link>
            )}
          </>
        )}
      </div>

      {/* Login / User Info */}
      <div className="flex items-center gap-4">
        {session ? (
          <div className="flex items-center gap-4">
            <img
              src={session.user?.image || "/default-avatar.png"}
              alt="Avatar"
              className="w-10 h-10 rounded-full border-2 border-blue-500"
            />
            <span className="text-gray-700 font-medium">{session.user?.name}</span>
            <button
              onClick={() => signOut({ callbackUrl: "/" })}
              className="px-4 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition"
            >
              Logout
            </button>
          </div>
        ) : (
          <button
            onClick={() => signIn("google")}
            className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition"
          >
            Login
          </button>
        )}
      </div>
    </nav>
  );
}
